:root {
  --link-on: #00acdc;
  --link-off: black;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  align-self: center;
  display: flex;
}

.navigation {
  align-items: center;
  display: flex;
}

.nav_list {
  margin: 0;
  padding-left: 0;
  list-style: none;
  display: flex;
}

.nav_item {
  display: flex;
}

.nav_link {
  color: var(--link-off);
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  font-size: 12px;
  text-decoration: none;
  display: flex;
}

.nav_link:visited {
  color: var(--link-off);
}

.nav_link:hover {
  color: var(--link-on);
}

.nav_link:active {
  color: var(--link-on);
  position: relative;
}

.nav_link.is-active:first-child {
  color: var(--link-on);
  margin-left: 0;
  position: relative;
}

.is-active:after {
  content: "";
  border-bottom: 4px solid var(--link-on);
  border-radius: 8px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
